<template>
  <nav class="mui-bar mui-bar-tab">
    <slot></slot>
  </nav>
</template>

<script>
/**
 * rs-tabbar
 * @module components/tabbar
 * @desc  tab tab-item
 * @param {*} value -  item component  id
 *
 * @example
 * <rs-tabbar v-model="selected">
 *   <rs-tab-item id="">
 *     <span slot="icon" class="mui-icon mui-home"></span>
 *     
 *   </rs-tab-item>
 * </rs-tabbar>
 *
 * <rs-tabbar v-model="selected">
 *   <rs-tab-item :id="['', '']">
 *     <span slot="icon" class="mui-icon mui-home"></span>
 *     
 *   </rs-tab-item>
 * </rs-tabbar>
 */
export default {
  name: 'rs-tabbar',
  props: {
    fixed: Boolean,
    value: {}
  }
}
</script>



